@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&family=Yeon+Sung&display=swap");
/*layout*/
body {
  background-color: #f6f6f6;
  display: flex;
  justify-content: center; /*flex일때 가로축의 center*/
  align-items: center; /*flex일때 세로축의 center*/
  font-family: "Nanum Pen Script", cursive;
  min-height: 100vh; /*viewpoint height 100%*/
  margin: 0;
}

#root {
  background-color: #ffffff;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px; /*box 그림자*/
}
.App {
  min-height: 100vh; /*viewpoint height 100%*/
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: 650px) {
  /*650px 이상 일때 적용*/
  .App {
    width: 640px;
  }
}

@media (max-width: 650px) {
  /*650px 이하 일때 적용*/
  .App {
    width: 90vw; /*viewpoint width 90%*/
  }
}

/* MyButton */
.MyButton {
  cursor: pointer;
  border: none;
  border-radius: 5px;

  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;

  font-size: 18px;
  white-space: nowrap; /*문자 자리가 부족해져도 아래로 내리지 않음*/
  font-family: "Nanum Pen Script", cursive;
}
.MyButton_default {
  background-color: #ececec;
  color: #000;
}
.MyButton_positive {
  background-color: #64c964;
  color: #fff;
}
.MyButton_negative {
  background-color: #fd565f;
  color: #fff;
}

/* MyHeader */
header {
  padding-top: 20px;
  padding-bottom: 20px;

  display: flex; /*block의 배치를 세로에서 가로로 변경*/
  align-items: center;
  border-bottom: 1px solid #e2e2e2;
}
header > div {
  display: flex;
}
header .head_text {
  width: 50%;
  font-size: 25px;
  justify-content: center;
}
header .head_btn_left {
  width: 25%;
  justify-content: start;
}
header .head_btn_right {
  width: 25%;
  justify-content: end;
}
header button {
  font-family: "Nanum Pen Script", cursive;
}

/* DiaryList */
.DiaryList .menu_wrapper {
  margin-top: 20px;
  margin-bottom: 30px;

  /* flex로 처리해서 컨텐츠를 양끝으로 */
  display: flex;
  justify-content: space-between;
}
.DiaryList .menu_wrapper .right_col {
  flex-grow: 1;
}
.DiaryList .menu_wrapper .right_col button {
  width: 100%;
}
.DiaryList .ControlMenu {
  margin-right: 10px;

  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;

  border: none;
  border-radius: 5px;
  background-color: #ececec;

  cursor: pointer;
  font-family: "Nanum Pen Script";
  font-size: 18px;
}

/* DiaryItem */
.DiaryItem {
  padding-top: 15px;
  padding-bottom: 15px;

  border-bottom: 1px solid #e2e2e2;

  display: flex;
  justify-content: space-between;
}

.DiaryItem .emotion_img_wrapper {
  cursor: pointer;
  min-width: 120px;
  height: 80px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
}
.DiaryItem .emotion_img_wrapper_1 {
  background-color: #64c964;
}
.DiaryItem .emotion_img_wrapper_2 {
  background-color: #9dd772;
}
.DiaryItem .emotion_img_wrapper_3 {
  background-color: #fdce17;
}
.DiaryItem .emotion_img_wrapper_4 {
  background-color: #fd8446;
}
.DiaryItem .emotion_img_wrapper_5 {
  background-color: #fd565f;
}
.DiaryItem .emotion_img_wrapper img {
  width: 50%;
}

.DiaryItem .info_wrapper {
  margin-left: 20px;
  flex-grow: 1;
  cursor: pointer;
}
.DiaryItem .diary_date {
  font-weight: bold;
  font-size: 25px;
  margin-bottom: 5px;
}
.DiaryItem .diary_content_preview {
  font-size: 18px;
}
.DiaryItem .btn_wrapper {
  min-width: 70px;
}

/* DiaryEditor */
/* .DiaryEditor {
} */
.DiaryEditor section {
  margin-bottom: 40px;
}
.DiaryEditor h4 {
  font-size: 22px;
  font-weight: bold;
}
.DiaryEditor .input_date {
  border: none;
  background-color: #ececec;
  border-radius: 5px;

  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;

  cursor: pointer;
  font-size: 20px;
  font-family: "Nanum Pen Script";
}

.DiaryEditor .emotion_list_wrapper {
  /* gid(격자)를 생성 */
  display: grid;
  /* 5개의 열을 auto로 지정 */
  grid-template-columns: repeat(5, auto);
  /* item사이의 간격 */
  gap: 2%;
}

.DiaryEditor textarea {
  font-family: "Nanum Pen Script";
  font-size: 20px;
  background-color: #ececec;
  border: none;
  border-radius: 5px;
  box-sizing: border-box;
  width: 100%;
  min-height: 200px;

  padding: 20px;

  /* 세로로만 크기 변경 가능 */
  resize: vertical;
}
.DiaryEditor .control_box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* EmotionItem */
.EmotionItem {
  border-radius: 5px;
  padding-top: 20px;
  padding-bottom: 20px;

  /* flex: 배치를 가로로 */
  display: flex;
  /* flex: direction: row를 다시 세로로(아래로 내림 */
  flex-direction: column;
  /* justify-content, align-items: 정중앙 배치 */
  justify-content: center;
  align-items: center;

  cursor: pointer;
}
.EmotionItem img {
  width: 50%;
  margin-bottom: 10px;
}
.EmotionItem span {
  font-size: 18px;
}

.EmotionItem_off {
  background-color: #ececec;
}
.EmotionItem_on_1 {
  background-color: #64c964;
  color: #fff;
}
.EmotionItem_on_2 {
  background-color: #9dd772;
  color: #fff;
}
.EmotionItem_on_3 {
  background-color: #fdce17;
}
.EmotionItem_on_4 {
  background-color: #fd8446;
}
.EmotionItem_on_5 {
  background-color: #fd565f;
}

/* Diary */
.DiaryPage {
}
.DiaryPage section {
  width: 100%;
  margin-bottom: 100px;

  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.DiaryPage h4 {
  font-size: 22px;
  font-weight: bold;
}
.DiaryPage .diary_img_wrapper {
  background-color: #ececec;
  width: 250px;
  height: 250px;
  border-radius: 5px;

  display: flex;
  flex-direction: column;
  align-items: center;
  /* 가로 중앙정렬에 적당히 가운데로 */
  justify-content: space-around;
}
.DiaryPage .emotion_discript {
  font-size: 25px;
  color: #fff;
}
.DiaryPage .diary_img_wrapper_1 {
  background-color: #64c964;
}
.DiaryPage .diary_img_wrapper_2 {
  background-color: #9dd772;
}
.DiaryPage .diary_img_wrapper_3 {
  background-color: #fdce17;
}
.DiaryPage .diary_img_wrapper_4 {
  background-color: #fd8446;
}
.DiaryPage .diary_img_wrapper_5 {
  background-color: #fd565f;
}
.DiaryPage .diary_content_wrapper {
  width: 100%;
  background-color: #ececec;
  border-radius: 5px;
  /* word-break: keep-all 줄바꾸기할때 단어를 쪼개지 않고 바꿈 */
  word-break: keep-all;
  /* overflow-wrap: break-word 문장이 너무 긴 경우 줄 내림 */
  overflow-wrap: break-word;
}
.DiaryPage .diary_content_wrapper p {
  padding: 20px;
  text-align: left;
  font-size: 20px;
  font-family: "Yeon Sung";
  font-weight: 400;
  /* 문장 위아래 간격 */
  line-height: 2.5;
}
